footer {
  background-color: var(--main-c-dark);
  height: 20rem;
  position: relative;
}

.footer--contact-info {
  gap: 5rem;
  display: flex;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer--contact-info-item:link, .footer--contact-info-item:visited {
  color: var(--c-white);
  font-size: var(--fs-med-m);
  text-decoration: none;
}

.footer--contact-info-item:hover, .footer--contact-info-item:active {
  cursor: pointer;
  text-decoration: underline;
}

.footer--property-msg {
  color: var(--c-white);
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
}

